import useStats from '../utils/useStats';

export default function CountriesSelector() {
  const stats = useStats('https://covid19.mathdro.id/api/countries');

  if (!stats) return <p> Loading.....</p>;
  console.log('shish', stats);
  return (
    <div>
      <select>{Object.entries(stats.countries.countries)}</select>
    </div>
  );
}
